.courses-wrapper {
  width: 80vw;
  margin-top: 10vh;
  margin-bottom: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  color:white;
  
  h1 {
    font-size: 3rem;
    line-height: 20px;
  }
  
  .grid {
    display: grid;
    margin-top: 40px;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 10px;
    margin-bottom: 50px;
  }
}