.skills-container {
  width: 80vw;
  margin-top: 10vh;
  margin-bottom: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;

  h1 {
    font-size: 3rem;
    line-height: 20px;
  }

  .skill-center{
    //background-color: green;
    height: 20vh;
    width: 50vw;
  }

  .skill-text {
    text-align: center;
    margin-top: 10vh;
    font-size: 1.5rem;
  }

  .skills-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin-top: 50px;
    grid-gap: 20px;

    .skill {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      opacity: .6;
      cursor: pointer;
      img {
        height: 3rem;
      }
      p {
        font-size: 14px;
      }
      &:hover{
        opacity: 1;
      }
    }
  }
}