.about-section {
  width: 80vw;
  margin-top: 10vh;
  margin-bottom: 10vh;
  display: flex;
  justify-content: space-between;
  color: white;

  .content {
    width: 50%;


    h1 {
      font-size: 3rem;
      line-height: 20px;
    }
  }

  .image-wrapper {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
  }
}
