.header-wrapper {
  color: white;
  background-size:100% 80%;
  width: 100%;
  height: 60vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: url("../images/sunset.png");
  background-attachment: fixed;

  h1{
    font-size: 5rem;
    text-align: center;
    line-height: 0px;
    margin-top: 20vh;
    font-weight: normal;
    border-bottom: 3px solid white;
    padding-bottom: 35px;
    text-shadow: 0px 2px black;
    box-shadow: 0px 2px black;
  }

  h2{
    margin-top: -20px;
    font-size: 1.5rem;
    font-weight: normal;
    text-shadow: 0px 2px black;
  }
}
