* {
  font-family: "Montserrat";
}

body {
  margin: 0;
  padding: 0;
  overflow-y: scroll;
  overflow-x: hidden;
  background-color: rgb(0, 0, 0);
}

.section {
  width: 100vw;
  height: auto;
  overflow: hidden;
  border-bottom: 3px solid white;
}

